'use strict';

class Star {

    constructor(size, canvasWidth, canvasHeight, player) {
        this.x = Math.random() * canvasWidth;
        this.y = Math.random() * canvasHeight;
        this.canvasWidth = canvasWidth;
        this.canvasHeight = canvasHeight;
        this.player = player;
        this.size = size;
    }

    update() {
        if (this.x < 0) this.x += this.canvasWidth;
        if (this.x > this.canvasWidth) this.x -= this.canvasWidth;
        if (this.y < 0) this.y += this.canvasHeight;
        if (this.y > this.canvasHeight) this.y -= this.canvasHeight;

        switch (this.size) {
        case 3:
            this.x -= this.player.vel.x;
            this.y -= this.player.vel.y;
            break;
        case 2:
            this.x -= 0.66 * this.player.vel.x;
            this.y -= 0.66 * this.player.vel.y;
            break;
        case 1:
            this.x -= 0.33 * this.player.vel.x;
            this.y -= 0.33 * this.player.vel.y;
            break;
        default:
            break;
        }
    }

    draw(ctx) {
        ctx.beginPath();
        ctx.rect(this.x, this.y, this.size, this.size);
        ctx.fillStyle = '#ffffff';
        ctx.fill();
        ctx.closePath();
    }
}

module.exports = Star;
